	//高德SDK
	import amap from '../../common/SDK/amap-wx.js';
	export default {

        
         onShow() {
         	  // 自提点列表
         	  this.request({
         	  	url: this.config.url + "order/store",
         	  	method: "post",
         	  	data: {
         	  		token: this.token,
         	  		lat: this.lat,
         	  		lng: this.lng,
         	  },
         	  	success: (res) => {
         	  		console.log("zi",res.data)
         	  		if (res.data.code == 1) {
         	  			this.selfList = res.data.data.data
         	  		} else {
         	  
         	  		}
         	  	}
         	  }),
         	
         	
         	//我的收获地址
         	this.request({
         		url: this.config.url + "member/address",
         		method: "post",
         		data: {
         			token: this.token,
         	
         		},
         		success: (res) => {
         			//console.log("wo",res.data.data, res)
         			if (res.data.code == 1) {
         				this.homeList = res.data.data
         			} else {
         	
         			}
         		}
         	})
         },

		onLoad() {
			var _this = this;



			var amapPlugin = new amap.AMapWX({
				//高德地图KEY，随时失效，请务必替换为自己的KEY，参考：http://ask.dcloud.net.cn/article/35070
				key: '5b9b64be2413fc19c26683fcf0de890f'
			});
			//定位地址
			amapPlugin.getRegeo({
				success: (data) => {
					//	console.log(data)
					this.city = data[0].regeocodeData.addressComponent.city.replace(/市/g, ''); //把"市"去掉

					this.lat = data[0].latitude; //纬度
					this.lng = data[0].longitude; //经度
					console.log(this.lat,this.lng)
					  // 自提点列表
					this.request({
						url: this.config.url + "order/store",
						method: "post",
						data: {
							token: this.token,
							lat: this.lat,
							lng: this.lng
					    },
						success: (res) => {
							console.log("zi",res.data)
							if (res.data.code == 1) {
								this.selfList = res.data.data.data
							} else {
					
							}
						}
					})
                }
			})

          
		},

		data() {
			return {
                homeNum:0,  //选中我的收货地址下标
				subState: 1,
				typeClass: 'self',
				city: "",
				addr: {},
				selfList: [],
				homeList: [],
				checked: "",
				lat:"" , //纬度
				lng:"",  //经度
			}
		},
		methods: {
			switchType(type) {

				this.typeClass = type;
				if (type == "home") {
					this.subState = 2;

				} else if (type == "self") {
					this.subState = 1;
				}
			},
			//跳转新增地址页面
			add() {
				
				uni.navigateTo({
					url: "/pages/user/address/edit/edit"
				})
			},
			//选取我的收货地址
			selectHome(id, home) {
				
                this.homeNum=id;
				
				var homeAddr=JSON.stringify(home)
				console.log(id, home, this.checked)
				uni.setStorage({key: "selectHome",data:1});
                uni.setStorage({
                		key: "address",
                		data: homeAddr,
                		success: function() {
                			//console.log("success")
                		}
                	})
                	uni.navigateTo({
                		url: "/pages/order/confirmation"
                
                	})
			},
			//选取自提点地址
			selectSelf(id, self) {
				var selfAddr = JSON.stringify(self)
				//console.log(id,self)
		         		uni.setStorage({
								key: "address",
								data: selfAddr,
								success: function() {
									//console.log("success")
								}
							})
						uni.navigateTo({
							url: "/pages/order/confirmation"
						                
						})
            }
		}
	}
